
'use client';

import { useState, useRef, useEffect } from 'react';
import { useUser, useDoc, useMemoFirebase } from '@/firebase';
import { doc, updateDoc } from 'firebase/firestore';
import { firestore } from '@/lib/firebase';
import Header from '@/components/layout/header';
import Footer from '@/components/layout/footer';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Badge } from '@/components/ui/badge';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Avatar, AvatarFallback, AvatarImage } from '@/components/ui/avatar';
import { X, ShieldCheck, Upload, Loader2 } from 'lucide-react';
import { Skeleton } from '@/components/ui/skeleton';
import { useAuthRedirect } from '@/hooks/useAuthRedirect';
import { verifySkillCertificate } from '@/ai/flows/verify-skill-certificate';
import { useToast } from '@/hooks/use-toast';
import { updateDocumentNonBlocking } from '@/firebase/non-blocking-updates';
import { arrayRemove, arrayUnion } from 'firebase/firestore';


interface Skill {
  name: string;
  verified: boolean;
}

export default function ProfilePage() {
  useAuthRedirect();
  const { user, isUserLoading } = useUser();
  const { toast } = useToast();
  const [newSkillToTeach, setNewSkillToTeach] = useState('');
  const [newSkillToLearn, setNewSkillToLearn] = useState('');
  const fileInputRef = useRef<HTMLInputElement>(null);
  const [selectedSkillForUpload, setSelectedSkillForUpload] = useState<string | null>(null);
  const [verifyingSkill, setVerifyingSkill] = useState<string | null>(null);

  const userDocRef = useMemoFirebase(() => (user ? doc(firestore, 'users', user.uid) : null), [user]);
  const { data: userProfile, isLoading: isProfileLoading } = useDoc<{
    name: string;
    email: string;
    photoURL: string;
    skillsToTeach: Skill[];
    skillsToLearn: { name: string }[];
  }>(userDocRef);

  const getInitials = (name: string | null | undefined) => {
    if (!name) return '';
    return name.split(' ').map((n) => n[0]).join('');
  };

  const handleAddSkill = (skillType: 'skillsToTeach' | 'skillsToLearn') => {
    if (!userDocRef) return;
    
    if (skillType === 'skillsToTeach') {
        const skillToAddName = newSkillToTeach.trim();
        if (skillToAddName === '' || userProfile?.skillsToTeach.some(s => s.name.toLowerCase() === skillToAddName.toLowerCase())) return;
        const newSkill: Skill = { name: skillToAddName, verified: false };
        updateDocumentNonBlocking(userDocRef, { skillsToTeach: arrayUnion(newSkill) });
        setNewSkillToTeach('');
    } else {
        const skillToAddName = newSkillToLearn.trim();
        if (skillToAddName === '' || userProfile?.skillsToLearn.some(s => s.name.toLowerCase() === skillToAddName.toLowerCase())) return;
        const newSkill = { name: skillToAddName };
        updateDocumentNonBlocking(userDocRef, { skillsToLearn: arrayUnion(newSkill) });
        setNewSkillToLearn('');
    }
  };

  const handleRemoveSkill = (skillType: 'skillsToTeach' | 'skillsToLearn', skillToRemove: Skill | {name: string}) => {
    if (!userDocRef) return;
    updateDocumentNonBlocking(userDocRef, {
      [skillType]: arrayRemove(skillToRemove),
    });
  };

  const handleUploadClick = (skillName: string) => {
    setSelectedSkillForUpload(skillName);
    fileInputRef.current?.click();
  };

  const handleFileChange = async (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (!file || !selectedSkillForUpload || !user || !userProfile) return;

    setVerifyingSkill(selectedSkillForUpload);
    const { id: toastId } = toast({
        title: "Verifying Certificate...",
        description: `Analyzing your certificate for ${selectedSkillForUpload}. Please wait.`,
    });

    try {
        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = async () => {
            const certificateDataUri = reader.result as string;

            const result = await verifySkillCertificate({
                userName: userProfile.name,
                skillToVerify: selectedSkillForUpload,
                certificateDataUri,
            });

            if (result.verified) {
                // Update the skill in Firestore
                const updatedSkills = userProfile.skillsToTeach.map(skill => 
                    skill.name === selectedSkillForUpload ? { ...skill, verified: true } : skill
                );
                await updateDoc(userDocRef!, { skillsToTeach: updatedSkills });

                toast({
                    variant: 'default',
                    title: "Skill Verified!",
                    description: `Your ${selectedSkillForUpload} skill is now verified.`,
                });
            } else {
                toast({
                    variant: "destructive",
                    title: "Verification Failed",
                    description: result.reason || `Could not verify the certificate for ${selectedSkillForUpload}.`,
                });
            }
        };
        reader.onerror = () => {
            throw new Error("Could not read file.");
        }

    } catch (error) {
        console.error("Verification failed", error);
        toast({
            variant: "destructive",
            title: "Upload Error",
            description: "Something went wrong during the verification process. Please try again.",
        });
    } finally {
        setVerifyingSkill(null);
        setSelectedSkillForUpload(null);
        if (fileInputRef.current) {
            fileInputRef.current.value = '';
        }
    }
  };
  
  const isLoading = isUserLoading || isProfileLoading;

  return (
    <div className="flex flex-col min-h-screen">
      <Header />
      <main className="flex-1 py-24">
        <div className="container mx-auto px-4 sm:px-6 lg:px-8">
          <Card className="max-w-4xl mx-auto bg-card/70 border-border/50">
            <CardHeader className="text-center p-8 border-b border-border/50">
              {isLoading ? (
                 <div className="flex flex-col items-center gap-4">
                    <Skeleton className="h-24 w-24 rounded-full" />
                    <Skeleton className="h-8 w-48" />
                    <Skeleton className="h-6 w-64" />
                 </div>
              ) : (
                <>
                <div className="flex justify-center">
                    <Avatar className="w-24 h-24 text-3xl mb-4 ring-4 ring-primary/50">
                        <AvatarImage src={userProfile?.photoURL || user?.photoURL || undefined} alt={userProfile?.name || ''} />
                        <AvatarFallback>{getInitials(userProfile?.name)}</AvatarFallback>
                    </Avatar>
                </div>
                <CardTitle className="text-3xl font-headline">{userProfile?.name}</CardTitle>
                <p className="text-muted-foreground">{userProfile?.email}</p>
                </>
              )}
            </CardHeader>
            <CardContent className="p-8 space-y-8">
              {/* Skills to Teach */}
              <div>
                <h3 className="text-xl font-headline mb-4">Skills I Can Teach</h3>
                <div className="flex gap-2 mb-4">
                  <Input
                    type="text"
                    placeholder="e.g., React"
                    value={newSkillToTeach}
                    onChange={(e) => setNewSkillToTeach(e.target.value)}
                    onKeyDown={(e) => e.key === 'Enter' && handleAddSkill('skillsToTeach')}
                  />
                  <Button onClick={() => handleAddSkill('skillsToTeach')}>Add</Button>
                </div>
                <div className="flex flex-wrap gap-2 items-center">
                  {isLoading ? (
                    <>
                        <Skeleton className="h-8 w-24 rounded-full" />
                        <Skeleton className="h-8 w-32 rounded-full" />
                        <Skeleton className="h-8 w-28 rounded-full" />
                    </>
                  ) : (
                    userProfile?.skillsToTeach?.map((skill) => (
                      <div key={skill.name} className="flex items-center gap-2 bg-secondary text-secondary-foreground rounded-full text-sm py-1 pl-3 pr-2">
                        {skill.name}
                        {skill.verified && <ShieldCheck className="w-4 h-4 text-accent" title="Verified Skill" />}
                        <button onClick={() => handleRemoveSkill('skillsToTeach', skill)} className="rounded-full hover:bg-background/20 p-0.5">
                          <X className="w-3 h-3" />
                        </button>
                        {!skill.verified && (
                            <Button
                                size="icon"
                                variant="ghost"
                                className="h-6 w-6 rounded-full"
                                onClick={() => handleUploadClick(skill.name)}
                                disabled={verifyingSkill !== null}
                                title={`Verify ${skill.name}`}
                            >
                                {verifyingSkill === skill.name ? (
                                    <Loader2 className="w-4 h-4 animate-spin" />
                                ) : (
                                    <Upload className="w-4 h-4 text-accent" />
                                )}
                            </Button>
                        )}
                      </div>
                    ))
                  )}
                </div>
              </div>
              
              {/* Skills to Learn */}
              <div>
                <h3 className="text-xl font-headline mb-4">Skills I Want to Learn</h3>
                <div className="flex gap-2 mb-4">
                  <Input
                    type="text"
                    placeholder="e.g., Python"
                    value={newSkillToLearn}
                    onChange={(e) => setNewSkillToLearn(e.target.value)}
                    onKeyDown={(e) => e.key === 'Enter' && handleAddSkill('skillsToLearn')}
                  />
                  <Button onClick={() => handleAddSkill('skillsToLearn')}>Add</Button>
                </div>
                <div className="flex flex-wrap gap-2">
                  {isLoading ? (
                    <>
                        <Skeleton className="h-8 w-20 rounded-full" />
                        <Skeleton className="h-8 w-36 rounded-full" />
                    </>
                  ) : (
                    userProfile?.skillsToLearn?.map((skill) => (
                      <Badge key={skill.name} variant="outline" className="text-base py-1 pl-3 pr-2 border-primary/50 text-primary">
                        {skill.name}
                        <button onClick={() => handleRemoveSkill('skillsToLearn', skill)} className="ml-2 rounded-full hover:bg-background/50 p-0.5">
                          <X className="w-3 h-3" />
                        </button>
                      </Badge>
                    ))
                  )}
                </div>
              </div>
            </CardContent>
          </Card>
        </div>
        <input
          type="file"
          ref={fileInputRef}
          onChange={handleFileChange}
          className="hidden"
          accept="image/png, image/jpeg, application/pdf"
        />
      </main>
      <Footer />
    </div>
  );
}
